input, button, textarea {
    outline: none;
    font-family: 'Work Sans', sans-serif;
    font-family: var(--work);
}
textarea {
    resize: none;
}

/* loader */

#loader {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 0 solid transparent;
    width: 150px;
    height: 150px;
    border-radius: 50%;
}
#loader::before, #loader::after {
    content: '';
    position: absolute;
    border: 1em solid #50CAEC;
    border: 1em solid var(--main-color);
    border-radius: 50%;
    width: inherit  ;
    height: inherit;
    top: 0;
    left: 0;
    -webkit-animation: loader 2s linear infinite;
            animation: loader 2s linear infinite;
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
#loader::before {
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}
@-webkit-keyframes loader {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
    }
}
@keyframes loader {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
    }
}

::selection {
    color: #bbb;
    background: #333333;
}

/* scrollbar */

body::-webkit-scrollbar {
    width: 8px;
    position: absolute;
}
body::-webkit-scrollbar-thumb {
    background: #000; 
    border-radius: 20px;
}
body::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.5); 
    cursor: none;
}
body::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.1);
}

/* header */
.header-main-container {
    position: fixed;
    width: 100%;
    z-index: 10000;
    background: rgba(255, 255, 255, 0);
}
.main-content-container {
    position: relative;
    top: 77px;
}
.section-content-contaniner {
    padding: 40px 0;
    width: 80%;
    margin: 0 auto;
}
.section-head-con h1 {
    padding: 4rem 0 45px;
    position: relative;
    font-size: 2.5rem;
}
.section-head-con h1::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    transition: -webkit-transform 2.5s ease-out;
    transition: transform 2.5s ease-out;
    transition: transform 2.5s ease-out, -webkit-transform 2.5s ease-out;
    -webkit-transform-origin: right;
            transform-origin: right;
}
.section-head-con-reveal h1::before {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
.section-head-con p {
    width: 70%;
    opacity: 0;
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
    transition: all 1.5s ease;
    transition-delay: 0.4s;
}
.section-head-con-reveal p {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

/* cursor */
.cursor {
    width: 2rem;
    height: 2rem;
    border: 2px solid #000;
    border-radius: 50%;
    position: absolute;
    z-index: 2000000;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    pointer-events: none;
    transition: -webkit-transform 0.3s linear;
    transition: transform 0.3s linear;
    transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}
.cursor-hover-ham {
    -webkit-transform: translate(-50%, -50%) scale(1.5);
            transform: translate(-50%, -50%) scale(1.5);
    z-index: 1999999;
    border: 1.5px solid #000;
}

/* nav slider */
.slider div:nth-child(1) {
    width: 500px;
}
.slider a {
    display: block;
    position: relative;
    top: 20%;
    left: 20%;
    margin: 10px 0;
    font-size: 2.2rem;
    color: rgba(0, 0, 0, 0.7);
    font-weight: bold;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform-origin: -50% -50%;
            transform-origin: -50% -50%;
}
.slider a:hover {
    color: rgba(0, 0, 0, 0.5);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

/* landing pages */

.land-page {
    height: 100vh;
    width: 100%;
    position: relative;
}
.land-page > .side-right {
    background: #50CAEC;
    background: var(--main-color);
    height: 100vh;
    width: 30%;
    position: absolute;
    top: 0;
    right: 0;
}
.land-page > .side-left {
    display: grid;
    align-items: center;
    position: relative;
    z-index: 10;
    height: 100%;
    width: 80%;
    margin: 0 auto;
}
.land-page > .side-left h1 {
    font-size: 3rem;
}

/* about section home page */

.about_container {
    width: 100%;
    margin: 80px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    justify-content: center;
    align-items: center;
    text-align: center;
    grid-gap: 30px;
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    transition: all 1.5s ease;
    padding: 60px 20px 20px;
} 
.about-circle {
    position: absolute;
    z-index: -1;
    width: 70px;
    height: 70px;
    background: var(--back);
    left: 30%;
    border-radius: 50%;
    top: -15%;
}
.about_container_content {
    position: relative;
    transition: color 0.3s ease;
    transition-delay: 0.3s;
    margin: 25px 0;
}

/* our services home page */
.grid_container_services {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-gap: 50px; 
    padding: 70px 0 40px;
}

.our_services_grid {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    transition: all 1.8s ease;
    overflow: hidden;
    position: relative;
    height: 528px;
}
.our_services_grid::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--back);
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.our_services_grid:hover.our_services_grid::before {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.our_services_grid h2 {
    position: absolute;
    top: 30px;
    text-transform: uppercase;
    left: 30px;
    font-size: 1.5rem;
    transition: color 0.3s ease;
    transition-delay: 0.2s;
}
.our_services_grid:hover > h2 {
    color: #fff;
}
.our_services_grid .content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff;
    padding: 30px 0;
    width: 80%;
    letter-spacing: 3px;
    opacity: 0;
    transition: opacity 0.3s ease;
    transition-delay: 0.2s;
}
.our_services_grid:hover > .content {
    opacity: 1;
}

/* products home page */

.product_content {
    display: flex;
    margin: 80px 0;
    font-family: 'Roboto Mono', monospace;
    font-family: var(--roboto-mono);
    padding: 30px 0;
}
.product_content div {
    flex: 1 1;
}
.product_content_row_reverse {
    flex-direction: row-reverse;
}
.product_content .content_container {
    display: grid;
    align-items: center;
}
.product_content .content_container {
    text-transform: uppercase;
    margin-left: 60px;
}
.product_content_row_reverse .content_container {
    text-align: right;
    margin-right: 60px;
}
.product_content img {
    width: 90%;
    box-shadow: 15px 33px 70px 10px #d5d5d5;
}

.product_content .content_container h1 {
    margin: 20px 0;
    font-size: 2rem;
    position: relative;
}
.product_content .content_container h1::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: right;
            transform-origin: right;
}
.product_content_reveal .content_container h1::before {
    -webkit-animation: textReveal 1.5s ease-in-out forwards;
            animation: textReveal 1.5s ease-in-out forwards;
}
@-webkit-keyframes textReveal {
    0% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
    100% {
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
    }
}
@keyframes textReveal {
    0% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
    100% {
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
    }
}
.go-to-project-link {
    padding: 10px 20px;
    border: 1.5px #50CAEC solid;
    border: 1.5px var(--main-color) solid;
    color: #50CAEC;
    color: var(--main-color);
    font-size: 15px;
    opacity: 0;
    position: relative;
    top: 30px;
    transition: all 1s ease-in-out;
    transition-delay: 0.3s;
} 
.go-to-project-link:hover {
    color: #fff;
}
.go-to-project-link::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: #50CAEC;
    background: var(--main-color);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: -webkit-transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
