@media screen and (max-width: 1150px) {
    .department_main_container .content {
        width: 300px;
    }
    .department {
        font-size: 1.4rem;
    }
}

/* 
    1. product content container font-size change 
    2. Views container changed to display block
*/
@media screen and (max-width: 980px) {
    /* product container */

    .product_content .content_container h1 {
        margin: 10px 0;
        font-size: 1.5rem;
    }

    /* views container */
    .views_container {
        display: block;
    }
    .views_container .content-con {
        margin-top: 60px;
    }
}

/* 
    1. change the padding at the bottom of the deparment main container after the view is changed 
    2. headings font size changed
    3. landing page heading size changed
    4. our hub head and contact info font-size changed 
    5. individual product page 2nd content section to flex column
*/
@media screen and (max-width: 900px) {
    /* department container */
    .department_main_container  {
        padding-bottom: 20px;
    }
    
    /* section headings container, our hubs heading */
    .section-head-con h1, .our-hub-head {
        font-size: 1.75rem;
    }
    .contact-info h1 {
        font-size: 1.5rem;
    }

    /* landing page */
    .land-page > .side-left h1 {
        font-size: 2.5rem;
    }

    .product-main-content-container .content {
        flex-direction: column-reverse;
    }
    .product-main-content-container .content section {
        font-size: 1.3rem;
    }
} 

/* 
    1. flex direction change of the project container
*/
@media screen and (max-width: 800px) {
    /* product container */
    .product_content_row {
        flex-direction: column-reverse;
    }
    .product_content_row_reverse {
        flex-direction: column-reverse;
    }
    .product_content .content_container {
        margin-bottom: 30px;
        text-align: left;
    }
    .product_content .content_container {
        margin-left: 0;
    }
    .product_content_row_reverse .content_container {
        margin-right: 0;
    }
}

/* 
    1. Links of the footer will be rotated
    2. Views content container to 1 element in a row
    3. Team container layout changed
    4. landing page heading size changed
    5. change the padding of the about container content
    6. address in the footer changed from grid 2 to grid 1
    7. change the product page layout from display grid to display block
    8. content of the product page
*/
@media screen and (max-width: 769px) {
    /* contact form */
    .footer_container {
        display: block;
        padding: 50px 0 0;
    }
    .footer_container .social_link_con {
        grid-template-columns: repeat(2, 1fr);
        text-align: center;
        margin-top: 40px;
        background: none;
        grid-gap: 10px;
    }
    .footer_container .msg-form-con div:nth-child(1) {
        display: none;
    }
    .footer_container .social_link_con a {
        -webkit-transform: rotate(0);
                transform: rotate(0);
        background: #fff;
        padding: 10px 0;
    } 

    /* views container */
    .views_container .content-con {
        grid-template-columns: repeat(1, 1fr);
    }

    /* team container */
    .team_container {
        grid-template-columns: repeat(2, 1fr);
    }

    /* landing page */
    .land-page > .side-left h1 {
        font-size: 1.75rem;
    }
    .land-page > .side-right {
        width: 50%;
    }

    /* about container content */
    .about_container {
        padding: 60px 0 20px;
    }

    /* contact info */
    .contact-info {
        grid-template-columns: 1fr;
        min-height: 100vh;
    }

    /* product page */
    .product-container {
        display: block;
        padding: 50px 0;
    }
    .product-container div:nth-child(1) {
        margin-bottom: 30px;
    }
    .product-container h1 {
        font-size: 2rem;
    }
    .product-container .back-link {
        top: 7px;
        left: -55px;
    }
    .product-container .back-link svg {
        height: 15px;
    }
}

/* 
    1. changing the heading font size
    2. font size of our services' headings changed
    3. font size of about container headings changed
    4. products headings font-size changed
    5. department content container headings changed
    6. message form heading font-size
*/
@media screen and (max-width: 600px) {
    /* section headings container */
    .section-head-con h1 {
        font-size: 1.25rem;
    }

    /* our servies container */
    .our_services_grid h2 {
        font-size: 1rem;
    }

    /* about container */
    .about_container h3 {
        font-size: 1rem;
    }

    /* product container */
    .product_content .content_container h1 {
        font-size: 1rem;
    }
    .product_content .content_container p {
        font-size: 13px;
    }

    /* department container */
    .department-mob-content h1 {
        font-size: 1rem;
    }

    /* message form */
    .msg-form-con h1 {
        font-size: 1.3rem;
    }

    .products-bottom-container .link_container .next-link {
        font-size: 1.3rem;
    }
    .products-bottom-container .link_container .next-link svg {
        height: 12px;
    }
}

/* 
    1. our services layout changed
    2. slider content put in the center and links font size changed
*/
@media screen and (max-width: 500px) {
    /* services container */
    .grid_container_services {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
    
    /* slider */
    .slider div:nth-child(1) {
        width: 100%;
    }
    .slider a {
        font-size: 1.5rem;
    }
}

